'use client';

import { useEffect, useState } from 'react';
// import { getFirestore, collection, getDocs } from 'firebase/firestore';
import { collection, getDocs, doc, updateDoc, deleteDoc, setDoc, getDoc } from "firebase/firestore";
import { initializeApp } from 'firebase/app';
import SideBar from '../../../components/SideBar/page';
import { db } from "../../../components/services/firebaseConfig";


// const firebaseConfig = {
//   apiKey: 'YOUR_API_KEY',
//   authDomain: 'YOUR_AUTH_DOMAIN',
//   projectId: 'YOUR_PROJECT_ID',
//   storageBucket: 'YOUR_STORAGE_BUCKET',
//   messagingSenderId: 'YOUR_MESSAGING_SENDER_ID',
//   appId: 'YOUR_APP_ID'
// };

// const app = initializeApp(firebaseConfig);
// const db = getFirestore(app);

const BulkScrapePage = () => {
  const [requests, setRequests] = useState([]);
  const [volunteers, setVolunteers] = useState([]);

  useEffect(() => {
    const fetchRequests = async () => {
      const querySnapshot = await getDocs(collection(db, 'bulk_scrap_requests'));
      const data = querySnapshot.docs.map(doc => ({ id: doc.id, ...doc.data() }));
      setRequests(data);
    };

    const fetchVolunteers = async () => {
      const querySnapshot = await getDocs(collection(db, 'Svolunteer'));
      const data = querySnapshot.docs.map(doc => ({ id: doc.id, ...doc.data() }));
      setVolunteers(data);
    };

    fetchRequests();
    fetchVolunteers();
  }, []);

  return (
    <div className="min-h-screen bg-gray-100 flex">
      <SideBar />
      <div className="flex-1 p-6">
        <h1 className="text-2xl font-semibold mb-4">Bulk Scrape Requests</h1>
        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-4">
          {requests.map(request => (
            <div key={request.id} className="bg-white shadow-md rounded-lg p-4">
              <h2 className="text-lg font-bold">{request.businessName}</h2>
              <p className="text-gray-600">Contact: {request.contactNumber}</p>
              <p className="text-gray-600">Organization: {request.organizationName}</p>
              <p className="text-gray-600">Material: {request.selectedMaterial}</p>
              <p className="text-gray-600">Quantity: {request.selectedQuantity}</p>
              <p className="text-gray-600">Special Request: {request.isSpecialRequest ? 'Yes' : 'No'}</p>
              <p className="text-gray-600">Note: {request.note}</p>
              <p className="text-gray-500">Status: {request.status}</p>
              <p className="text-gray-500">Timestamp: {request.timestamp}</p>
              <p className="text-gray-500">User ID: {request.userId}</p>
              <p className="text-sm text-gray-500">Assigned Volunteer: {request.volunteer || 'None'}</p>
            </div>
          ))}
        </div>

        <h2 className="text-xl font-semibold mt-6">Volunteers</h2>
        <table className="w-full mt-4 bg-white shadow-md rounded-lg">
          <thead>
            <tr className="bg-gray-200">
              <th className="p-2">Name</th>
              <th className="p-2">Email</th>
              <th className="p-2">Role</th>
            </tr>
          </thead>
          <tbody>
            {volunteers.map(volunteer => (
              <tr key={volunteer.id} className="border-b">
                <td className="p-2">{volunteer.name}</td>
                <td className="p-2">{volunteer.email}</td>
                <td className="p-2">{volunteer.role}</td>
              </tr>
            ))}
          </tbody>
        </table>
      </div>
    </div>
  );
};

export default BulkScrapePage;